import React from 'react';
import PropTypes from 'prop-types';
import { platform, IOS } from '@vkontakte/vkui';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import Button from '@vkontakte/vkui/dist/components/Button/Button';
import Group from '@vkontakte/vkui/dist/components/Group/Group';
import Cell from '@vkontakte/vkui/dist/components/Cell/Cell';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Avatar from '@vkontakte/vkui/dist/components/Avatar/Avatar';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeaderButton from '@vkontakte/vkui/dist/components/PanelHeaderButton/PanelHeaderButton';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import './StyleMainMenu.css';
import cloud from '../img/cloud.png';

const osName = platform();

const MainMenu = (props) => (
	<Panel id={props.id}>
		<PanelHeader left =
			{<PanelHeaderButton onClick={props.go} data-to="startMenu"> 
			 {osName === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			 </PanelHeaderButton>}>
				Учебная комната
		</PanelHeader>
		<Div className="ThemeMain">
			<Div className="CloudMain">
			<Div align="center">Привет!,</Div>
			<Div align="center">что ты хочешь сделать?</Div>
			</Div>
			<Div className = "ButtonStartMenuMain">
				<Button className="ButtonThemeMain" size="xl" mode="outline" onClick={props.go} data-to="scheduleMenu">
					<Div className="TextButtonMain">Записаться</Div>
				</Button>
			</Div>
			<Div className = "ButtonStartMenuMain">
				<Button className="ButtonThemeMain" size="xl" mode="outline"  onClick={props.go} data-to="myRecordMenu"> 
					<Div className="TextButtonMain">Посмотреть мои записи</Div>
			 	</Button>
			</Div>
		</Div>
	</Panel>
);


MainMenu.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default MainMenu;